export default {
    name:'logout',
    created:function () {
      // 1.修改vuex状态数据
      // 直接调用  省下了...mapMutations 引入了
      this.$store.commit('mutationLogout');
      // 2.清空本地存储数据
      window.localStorage.clear();
      // 3.页面跳转
      this.$router.push('/topiclist/all');
    }
}



