
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import {mapState,mapGetters,mapMutations} from 'vuex'
export default {
name: 'app',
computed:{
...mapState(['logined','loginedMoreMenus','unLoginedMoreMenus','isShowMoreMenus','moreMenus','topics']),
...mapGetters(['moreMen']),







isShowMoreMenus:{

get:function(){
return this.$store.state.isShowMoreMenus
},

set:function(nval){
this.$store.commit('mutationsisShowMoreMenus',nval)
}
}
},
created:function () {
var loginStatus = window.localStorage.getItem('loginStatus')

if(loginStatus){
this.mutationIsLogined();
this.mutationLonginname(window.localStorage.getItem('loginName'));
this.mutationLonginname(window.localStorage.getItem('accesstoken'));
}
},
data:function(){
return {





















}
},
methods:{
...mapMutations(['mutationIsLogined','mutationLonginname','mutationAccesstoken']),
handClick:function(menuKey, menuItem){


this.$router.push('/'+menuKey)
},

showMoreMenus:function(){
this.isShowMoreMenus=true;
},
routeto:function(index){

this.$router.push("/topiclist/"+this.topics[index].name)
}
},
components: {
Tab,
TabItem,
XHeader,
Actionsheet
}
}



