import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store =new Vuex.Store({
    state:{
        logined:false,
        loginName:'',//登录名称
        accesstoken:'',//token码
        loginedMoreMenus:{
          center:"个人中心",
          msg:"更多通知",
          logout:'登出'
        },
        unLoginedMoreMenus:{
          login:"登录"
        },
        isShowMoreMenus:false,
        moreMenus:{
          login:'登录'
        },
        topics:[
          {name:'all',desc:'全部'},
          {name:'good',desc:'精品'},
          {name:'share',desc:'分享'},
          {name:'ask',desc:'回答'},
          {name:'job',desc:'招聘'},
          {name:'test',desc:'测试'},
        ]
    },
    getters:{  
        moreMen(state){
        // state表示当前store对象的state
        if(state.logined){
          return state.loginedMoreMenus
        }
        return state.unLoginedMoreMenus;
      },
    },
    mutations:{
        mutationsisShowMoreMenus:function(state,payload){
            // 将数值换成用户传入的值
            state.isShowMoreMenus=payload;
        },                                        
        mutationIsLogined:function(state){
          state.logined=true;
        },
        // 修改登录名
        mutationLonginname:function(state,payload){
          state.loginName=payload;
        },
         // 修改accesstoken
         mutationAccesstoken:function(state,payload){
          state.accesstoken=payload;
        },
        // 用户注销
        mutationLogout(state){
          state.logined=false;
          state.logiName='';
          state.accesstoken='';
        }
    },
    actions:{}
})
export default store;


